:root {
  --nav-width: 110px;
  --primary-color: #4d8b85;
  --primary-light: #9fcbc1;
  --menu-icons-size: 40px;
  --lg-avatar-size: 200px;
  --sm-avatar-size: 24px;
	--sub-menu-height: 75px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: relative;
  font-family: Raleway, sans-serif;
  font-size: 12px;
}
.content-container {
  position: absolute;
  top: 0;
  left: var(--nav-width);
  width: calc(100vw - var(--nav-width));
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 20px;
  overflow: scroll;
  align-items: center;
}
.client-info-container {
  position: absolute;
  top: 75px;
  left: var(--nav-width);
  bottom: 0;
  width: calc(100vw - var(--nav-width));
  height: calc(100vh - var(--sub-menu-height));
  display: flex;
  flex-direction: row;
  overflow: auto;
}
.content-container p,
.client-info-container p {
  font-size: 14px;
  margin-bottom: 10px;
}
h1 {
  text-align: center;
  margin-bottom: 24px;
  font-size: 24px;
}
a {
  text-decoration: none;
  color: inherit;
}

/* MENU */

nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: var(--nav-width);
  background-color: var(--primary-color);
  z-index: 100;
  color: #fff;
  display: flex;
  flex-direction: column;
  padding-top: 40px;
}
.mainMenu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.menu-item {
  width: 100%;
  padding: 10px 30px;
  display: flex;
  justify-content: flex-start;
  font-size: 18px;
  cursor: pointer;
  flex-direction: column;
  align-items: center;
}
.menu-item i {
  font-size: var(--menu-icons-size);
}

.menu-item span {
  font-size: 12px;
}
.menu-item:hover,
.active {
  background: #ffffff44;
}
/* SUB MENU */
.sub-menu {
  position: absolute;
  top: 0;
  left: var(--nav-width);
  width: calc(100% - var(--nav-width));
  backgroundcolor: red;
  height: var(--sub-menu-height);
  padding: 11px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
/* CLIENTS */
.clients-list-container {
  width: 100%;
  max-width: 1200px;
  background: #f7f9fb;
  border-radius: 16px;
  padding: 24px;
}
.clients-list {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.client {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 11px 25px;
  font-size: 12px;
  gap: 20px;
  color: #1c1c1c;
}

.client:not:first-child  {
  cursor: pointer;
}

.client > div:nth-child(3),
.client > div:nth-child(4) {
  text-align: center;
}
.client > *:first-child,
.client > div:nth-child(2) {
  width: calc((100% - 360px) / 2);
}
.client > div:nth-child(3) {
  width: 200px;
}
.client > div:nth-child(4) {
  width: 100px;
}

.name-content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.table-title {
  color: rgba(0, 0, 0, 0.4);
  padding-bottom: 10px !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
	margin-bottom: 20px;
}
.avatar {
  border-radius: 50%;
  background-color: #bbb;
  overflow: hidden;
  position: relative;
}

.avatar img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.avatar h1 {
  text-align: center;
  margin-bottom: 24px;
  font-size: 20px;
}
.sm {
  width: var(--sm-avatar-size);
  height: var(--sm-avatar-size);
  margin-right: 10px;
}

.lg {
  width: var(--lg-avatar-size);
  height: var(--lg-avatar-size);
  margin: 0 auto;
}

.tags-container {
  margin-top: 40px;
}

.tag {
  background-color: #eee;
  margin-right: 5px;
  padding: 2px 16px 4px;
  border-radius: 14px;
}

/* ORDERS */

.orders-list-container {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  background: #f7f9fb;
  border-radius: 16px;
  padding: 24px;
}
.orders-list {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(6, 1fr);
  padding: 11px 20px;
}
.orders-list > div:not(:first-child) {
  text-align: center;
}

.client-personal-info-container {
  width: 450px;
  display: flex;
  gap: 30px;
  flex-direction: column;
  padding: 40px;
  background-color: rgb(221, 221, 221);
	background: #F7F9FB;
border-radius: 16px;
margin: 0 20px 20px;
}
.order-status {
  font-size: 16px;
}

.order-client-info {
  border: 1px solid #ccc;
  padding: 20px;
  margin-top: 20px;
  font-size: 16px;
  line-height: 1.5rem;
}
.order-client-info-title {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 10px;
}
.status {
  text-align: center;
}
.status span {
  border-radius: 8px;
  padding: 5px 10px;
}

/* PROJECT */
.project-title {
  margin-bottom: 20px;
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.project-table {
  width: 100%;
  margin-top: 40px;
  display: flex;
}
.project-step {
  width: calc(auto - 1px);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  overflow: hidden;
  border-right: 1px solid #fff;
}
.project-stages-title {
  color: #fff;
  background-color: #000;
  width: 100%;
  text-align: center;
  padding: 10px;
}
.project-steps-title {
  display: flex;
  text-align: center;
  align-content: stretch;
}
.project-steps-title div {
  display: flex;
  overflow: hidden;
  padding: 20px 10px;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #fff;
  font-weight: bold;
  height: 70px;
  font-size: 12px;
}

.project-item {
  display: flex;
  width: 100%;
}
.project-item div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.done {
  background-color: #bde2a8 !important;
}

.in-progress {
  background-color: #ffdf84 !important;
}

.paused {
  background-color: #00bcd4 !important;
}

.custom-dropdown {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #fff;
  width: 100%;
}

.custom-dropdown select {
  padding: 10px;
  background-color: #efefef;
  color: #000;
  font-size: inherit;
  border: 0;
  margin: 0;
  height: 50px;
  width: 100%;
  border-radius: 3px;
  text-indent: 0.01px;
  border-radius: 10px;
  overflow: hidden;
  text-align: center !important;
  -webkit-appearance: none; /* hide default arrow in chrome OSX */
}

.custom-dropdown::before,
.custom-dropdown::after {
  content: "";
  position: absolute;
  pointer-events: none;
}

.custom-dropdown select[disabled] {
  color: rgba(0, 0, 0, 0.3);
}

.custom-dropdown select[disabled]::after {
  color: rgba(0, 0, 0, 0.1);
}

/* Calendar */

.month-container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 100%;
  max-width: 1500px;
  color: #000;
  align-content: center;
  margin: 0 auto 10px auto;
  background-color: #fff;
  border-radius: 1.5em;
  padding: 20px;
}

.month-container div.item {
  height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  font-size: 14px;
  border: 2px solid #fff;
  padding: 10px;
  overflow: scroll;
  border-radius: 12px;
  background-color: #efefef;
}
.month-container div.day-number {
  margin-bottom: 10px;
}
.month-container h3 {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: bold;
}
.month-title,
.button-container {
  grid-column: 1 / -1;
  padding: 0 20px;
  height: 25px !important;
  border: none !important;
}

.month-title {
  width: 100%;
  text-align: center;
}

.button-container {
  justify-content: space-between !important;
  border-bottom: 1px solid #ddd;
  border-radius: 0 !important;
  margin-bottom: 15px;
  display: flex;
  flex-direction: row;
}

.button-container button {
  min-width: 50px;
  border: none;
  font-size: 24px;
  background-color: transparent;
  cursor: pointer;
}
.event-container {
  width: 100%;
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 5px;
  margin-bottom: 2px;
  cursor: pointer;
}
.personal {
  background-color: #ffb6cf;
}

.family {
  background-color: #ffc107;
}
.work {
  background-color: var(--primary-light);
}
.holidays {
  background-color: #ddd;
}
.white {
  background-color: white !important;
}

/* PAGINATION */

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}
.pagination li {
  list-style: none;
  min-width: 40px;
  height: 40px;
  margin-right: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 10px;
  background: #efefef;
  border-radius: 12px;
}
.pagination li.active {
  background-color: var(--primary-light);
}

/* preview window */
.preview-window {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.preview-window .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.window-container {
  position: absolute;
  top: 0;
  right: 0;
  width: 400px;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #fff;
  padding: 40px;
}
.window-container .close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 20px;
  color: #000;
  border-radius: 50%;
  cursor: pointer;
}

/* EVENT INFO */
.event-wrapper .event-title {
  margin-bottom: 20px;
}
.event-info {
  font-size: 16px;
  line-height: 1.5rem;
}
